<template>
  <div class="home">
    <div class="container flex flex-wrap justify-around">
      <div class="w-full">ベース
        <select v-model="base_change" class="bg-black bg-opacity-50 outline-none m-1 ">
          <option value=""></option>
          <option value="ノンアルコール">ノンアルコール</option>
          <option value="ジン">ジン</option>
          <option value="ウォッカ">ウォッカ</option>
          <option value="テキーラ">テキーラ</option>
          <option value="ラム">ラム</option>
          <option value="ウイスキー">ウイスキー</option>
          <option value="ブランデー">ブランデー</option>
          <option value="リキュール">リキュール</option>
          <option value="ワイン">ワイン</option>
          <option value="ビール">ビール</option>
          <option value="日本酒">日本酒</option>
        </select>
      </div>
      <div class="w-full">味わい
        <select v-model="taste_change" class="bg-black bg-opacity-50 outline-none m-1">
          <option value=""></option>
          <option value="甘口">甘口</option>
          <option value="中甘口">中甘口</option>
          <option value="中口">中口</option>
          <option value="中辛口">中辛口</option>
          <option value="辛口">辛口</option>
        </select>
      </div>
      <div class="w-full">スタイル
        <select v-model="style_change" class="bg-black bg-opacity-50 outline-none m-1">
          <option value="" ></option>
          <option value="ショート">ショート</option>
          <option value="ロング">ロング</option>
        </select>
      </div>
      <div class="w-full">技法
        <select v-model="technique_change" class="bg-black bg-opacity-50 outline-none m-1">
          <option value=""></option>
          <option value="ビルド">ビルド</option>
          <option value="ステア">ステア</option>
          <option value="シェイク">シェイク</option>
          <option value="ブレンド">ブレンド</option>
        </select>
      </div>
    </div>
    <button v-on:click="show" class="button m-3 text-base border-white border py-2 px-4">注文</button>
      <modal name="modal-test" :draggable="false" :resizable="false" :scrollable="true" width="85%" height="auto">
        <div v-for="(cocktail,key) in cocktailSelect" :key="key" class="my-3 border-b-2">
          <small class="text-black">{{cocktail.cocktail_digest}}</small><br>
          <u class="text-xl text-black">{{cocktail.cocktail_name}}</u>
          <div class="flex justify-between mx-3">
            <div class="bg-red-900 rounded-md px-2">Base</div><div class="text-black">{{cocktail.base_name}}</div>
            <div class="bg-red-900 rounded-md px-2">Taste</div><div class="text-black">{{cocktail.taste_name}}</div>
          </div>
        <div class="flex justify-between mx-3">
          <div class="bg-red-900 rounded-md px-2">Style</div><div class="text-black">{{cocktail.style_name}}</div>
          <div class="bg-red-900 rounded-md px-2">Alc.</div><div class="text-black">{{cocktail.alcohol}}%</div>
        </div>
        <h1 class="text-black text-left m-2">{{cocktail.cocktail_desc}}</h1>
        </div>
      </modal>
  </div>
</template>

<script>
import axios from 'axios'
export default{
  data() {
    return{
      base_change:'',
      taste_change:'',
      style_change:'',
      technique_change:'',
      cocktails: [],
    }
  },
  computed: {
    cocktailSelect: function() {
//-------------------- 全ての項目を入力 --------------------------------
    // cocktailSelect: function() {
      // return this.cocktails.filter(
      //   cocktails => cocktails.base_name === this.base_change 
      //   && cocktails.taste_name === this.taste_change 
      //   && cocktails.style_name === this.style_change 
      //   && cocktails.technique_name === this.technique_change
      // );
//--------------------------------------------------------------------

//--------------------- 原先生案 --------------------------------------
      let result1 = ''    // base_changeで絞り込まれた内容が入る
      let result2 = ''    // taste_change...
      let result3 = ''
      let result4 = ''

      if(this.base_change == '') {
        // base_changeが空の時
        result1 = this.cocktails
      } else {
        // base_changeが選ばれている時
        result1 = this.cocktails.filter(
          cocktails => cocktails.base_name === this.base_change
        )
      }
      if(this.taste_change == '') {
        // taste_changeが空の時
        result2 = result1
      } else {
        // taste_changeが選ばれている時
        result2 = result1.filter(
          cocktails => cocktails.taste_name === this.taste_change
        )
      }
      if(this.style_change == '') {
        // style_changeが選ばれていない時
        result3 = result2
      } else {
        // style_changeが選ばれている時
        result3 = result2.filter(
          cocktails => cocktails.style_name === this.style_change
        )
      }
      if(this.technique_change == '') {
        // technique_changeが選ばれていない時
        result4 = result3
        return result4
      } else {
        // technique_changeが選ばれている時
        result4 = result3.filter(
          cocktails => cocktails.technique_name === this.technique_change
        )
        return result4
      }
  },
//--------------------------------------------------------------------
  },
  methods: {
    show : function() {
      this.$modal.show('modal-test');
    },
    hide : function () {
      this.$modal.hide('modal-test');
    },
  },
    created() {
      axios.get(
        "https://cocktail-f.com/api/v1/cocktails"
        )
    .then(response => {
      console.log(response);
      this.cocktails = response.data.cocktails;
      })
    },
  }

</script>